import React, { FC } from "react";
import { Col, Row } from "antd";
import Fade from "react-reveal/Fade";
import styles from "./styles.module.scss";
import useTyped from "../../hooks/useTyped";
import useLottie from "../../hooks/useLottie";
import { arrowDownLottie } from "./constants";
import avatar from "../../assets/icons/avatar.png";

const strings = [
  "Graphic Design.",
  "UI Design.",
  "Project Management.",
  "Function Design.",
];

const Home: FC = () => {
  const el = useTyped(strings, { loop: true });
  const arrowDownLottieRef = useLottie(arrowDownLottie);

  const next = () => {
    const $about = document.querySelector("#about");
    if ($about) {
      $about.scrollIntoView({ behavior: "smooth" });
    }
  };

  return (
    <section id="home" className={styles.home}>
      <Row className={styles.content}>
        <Col span={20} md={15} className={styles.intro}>
          <p>I'm Wenmin Chen.</p>
          <p>
            Proudly to introduce an AR pet game, <br />
            <i className={styles.lulu}>Lulu Dancing !</i>
          </p>
          <p>I'm responsible for </p>
          <p>
            <span className={styles.sometime} ref={el} />
          </p>
        </Col>

        <Col span={24} md={9} className={styles.ballWrapper}>
          <Fade bottom>
            <div className={styles.ball}>
              <img width={260} src={avatar} alt="Wenmin Avatar" />
            </div>
          </Fade>
        </Col>

        <Col className={styles.next} span={20}>
          <Fade top>
            <div className={styles.handDown}>
              <div ref={arrowDownLottieRef} onClick={next} />
            </div>
          </Fade>
        </Col>
      </Row>
    </section>
  );
};

export default Home;
